import { BRAND_INSIGHTS_TABS } from "@/constants/brand-insights";
<template>
  <div id="brand-insights-component">
    <div class="search-bar-container">
      <SearchBar></SearchBar>
    </div>
    <div class="tabs-container">
      <el-tabs class="tabs" v-model="tabValue" @tab-click="onTabClick">
        <el-tab-pane label="Brand Summary" name="BrandSummary"></el-tab-pane>
        <el-tab-pane label="Brand Power" name="BrandPower"></el-tab-pane>
        <el-tab-pane label="Product Power" name="ProductPower"></el-tab-pane>
        <el-tab-pane
          label="Financial Power"
          name="FinancialPower"
        ></el-tab-pane>
      </el-tabs>
    </div>

    <el-main class="child-router-container">
      <router-view></router-view>
    </el-main>
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue";
import { useRoute, useRouter } from "vue-router";

import SearchBar from "@/components/SearchBar.vue";

import { Pane } from "element-plus/lib/el-tabs/src/tabs.vue";

const Component = defineComponent({
  name: "BrandInsights",
  components: {
    SearchBar
  },
  setup() {
    const router = useRouter();
    const route = useRoute();

    const tabValue = ref("");

    const onTabClick = (value: Pane) => {
      router.push({ name: value.props.name });
    };

    onMounted(() => {
      if (route.name) {
        tabValue.value = route.name as string;
      }
    });

    return {
      tabValue,
      onTabClick
    };
  }
});

export default Component;
</script>

