
import { defineComponent, ref, toRefs, onMounted, nextTick } from "vue";
import * as echarts from "echarts/core";
import { RadarChart, RadarSeriesOption } from "echarts/charts";
import { GridComponent, GridComponentOption } from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";

type ECOption = echarts.ComposeOption<RadarSeriesOption | GridComponentOption>;

const option: ECOption = {
  radar: {
    // shape: 'circle',
    name: {
      textStyle: {
        color: "#fff",
        backgroundColor: "#999",
        borderRadius: 3,
        padding: [3, 5]
      }
    },
    indicator: [
      { name: "销售（sales）", max: 6500 },
      { name: "管理（Administration）", max: 16000 },
      { name: "信息技术（Information Techology）", max: 30000 },
      { name: "客服（Customer Support）", max: 38000 },
      { name: "研发（Development）", max: 52000 },
      { name: "市场（Marketing）", max: 25000 }
    ]
  },
  series: [
    {
      name: "预算 vs 开销（Budget vs spending）",
      type: "radar",
      // areaStyle: {normal: {}},
      data: [
        {
          value: [4300, 10000, 28000, 35000, 50000, 19000],
          name: "预算分配（Allocated Budget）"
        },
        {
          value: [5000, 14000, 28000, 31000, 42000, 21000],
          name: "实际开销（Actual Spending）"
        }
      ]
    }
  ]
};

echarts.use([RadarChart, GridComponent, CanvasRenderer]);

const Component = defineComponent({
  name: "RadarChartCard",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup(props) {
    const { title } = toRefs(props);
    const dialogVisible = ref(false);

    const onExpandClick = async () => {
      dialogVisible.value = true;
      await nextTick();
      const chart = echarts.init(
        document.getElementById("radar-chart-dialog-" + title.value)!
      );
      chart.setOption(option);
    };

    onMounted(() => {
      const chart = echarts.init(
        document.getElementById("radar-chart-main-" + title.value)!
      );
      chart.setOption(option);
    });

    return {
      dialogVisible,

      onExpandClick
    };
  }
});

export default Component;
