<template>
  <div id="pie-chart-card-component">
    <div class="top-container">
      <h3 class="title">{{ title }}</h3>
      <div class="expand-button" @click="onExpandClick">
        <img src="@/assets/images/icons/png/expand.png" alt="" />
      </div>
    </div>
    <div class="chart-container">
      <div :id="'pie-chart-main-' + title" class="chart-main"></div>
    </div>

    <el-dialog :title="title" v-model="dialogVisible" width="50%">
      <div class="dialog-container">
        <div :id="'pie-chart-dialog-' + title" class="chart-dialog"></div>
      </div>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, onMounted, nextTick } from "vue";
import * as echarts from "echarts/core";
import { PieChart, PieSeriesOption } from "echarts/charts";
import {
  GridComponent,
  GridComponentOption,
  LegendComponent,
  LegendComponentOption
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";

type ECOption = echarts.ComposeOption<
  PieSeriesOption | GridComponentOption | LegendComponentOption
>;

echarts.use([PieChart, GridComponent, LegendComponent, CanvasRenderer]);

const Component = defineComponent({
  name: "PieChart",
  props: {
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Object,
      default: () => ({})
    }
  },
  setup(props) {
    const { title } = toRefs(props);
    const dialogVisible = ref(false);

    const option: ECOption = {
      legend: {
        orient: "vertical",
        right: 40,
        top: "middle"
      },
      series: [
        {
          name: "访问来源",
          type: "pie",
          radius: "50%",
          data: [
            { value: 1048, name: "搜索引擎" },
            { value: 735, name: "直接访问" },
            { value: 580, name: "邮件营销" },
            { value: 484, name: "联盟广告" },
            { value: 300, name: "视频广告" }
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    };

    const onExpandClick = async () => {
      dialogVisible.value = true;
      await nextTick();
      const chart = echarts.init(
        document.getElementById("pie-chart-dialog-" + title.value)!
      );
      chart.setOption(option);
    };

    onMounted(() => {
      const chart = echarts.init(
        document.getElementById("pie-chart-main-" + title.value)!
      );
      chart.setOption(option);
    });

    return {
      dialogVisible,

      onExpandClick
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#pie-chart-card-component
  width 100%
  background-color white

  .top-container
    display flex
    justify-content space-between
    align-items center
    padding 20px 30px 0
    .title
      font-size $font-size-lg2x
      font-weight 500
    .expand-button
      cursor pointer
      img
        width 20px
        height 20px

  .chart-container
    width 100%
  .dialog-container
    width 100%
  .chart-main
    chart-main()
  .chart-dialog
    chart-dialog()
</style>
